import Image from "next/image";
import styles from "./page.module.css";
import ProductList from "./component/ProductList";
import { Product } from './component/interface';
import banner from "./img/bnpolo.jpg";
export default async function Home() {
  let products: Product[] = await getProduct("http://localhost:3450/product");
  const featuredProducts = products.slice(0, 4); 
  
  return (
    <div>
      {/* Banner */}
      <div className={styles.banner}>
        <Image 
          src={banner}
          alt="Banner quảng cáo"
          layout="fill"
          objectFit="cover" 
          priority
        />
      </div>

      {/* Danh sách sản phẩm */}
      <ProductList props={{
        title:"Sản Phẩm Nổi Bật", 
        products: featuredProducts
      }}/>

      <div className={styles.newProductsSection}>
        <ProductList props={{
          title:"Sản Phẩm Mới", 
          products: products
        }}/>
      </div>

      <ProductList props={{
        title:"Tất Cả Sản Phẩm", 
        products: products
      }}/>
    </div>
  );
}

async function getProduct(url:string) {
  let res = await fetch(url);
  let data = await res.json();
  let products = data.map((p: Product) => {
    return {
      name: p.name,
      price: p.price,
      image: p.image,
      description: p.description,
      category: p.category
    };
  });
  return products;
}
